.column {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    width: 90%;
    margin-left: 5%;
    align-self: flex-start;
    align-items: flex-start;
    justify-content: flex-start;
}


.row {
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    align-self: flex-start;
    justify-content: space-between;
    width: 100%;
    gap: 30px;
}


.row span {
    color: var(--Black, #000);
    /* Heading/H5 */
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    /* 33.6px */
}

.filter-clear {
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: center;
    justify-content: space-between;
    width: 20%;
}

.tags-sort {
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: center;
    justify-content: space-between;
    width: 100%;
}

.tags-sort {
    width: 60%;
}

.view-all {
    display: flex;
    flex-direction: row-reverse;
    width: 60%;
    align-items: flex-end;
    right: 0px;
}

.column-h3 {
    color: var(--Black, #000);
    /* Heading/H5 */
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    /* 33.6px */
}

.column-h5 {
    color: var(--Black, #000);
    /* Heading/H5 */
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    text-align: left;
    /* 33.6px */
}

.filters {
    display: flex;
    flex-direction: column;
    width: 30%;
}

.results {
    display: flex;
    flex-direction: column;
    border: dashed;
    border-color: rgb(163, 164, 163);
    height: fit-content;
    width: fit-content;
    padding-right: 40px;
}

.no-results-found {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 80px;
    border: solid;
    padding: 2px;
    width: 100%;
}

.filters-column {
    display: flex;
    flex-direction: column;
    width: 20%;
    height: 1000px;
    align-items: flex-start;
    gap: 20px;
}

.column-filter-spacer {
    display: block;
    border-bottom: 1px solid #000000;
    width: 100%;
    padding-bottom: 20px;
}

.collapsable-filters {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    align-items: flex-start;
}

.filter-type {
    display: flex;
    flex-direction: row;
    font-weight: 700;
    align-items: flex-start;
    justify-items: flex-start;
    justify-content: space-between;
    width: 100%;

}

.filter-type div {
    display: flex;
    align-items: flex-start;
    right: 0px;
}

.filters {
    width: 100%;
}

.option {
    display: flex;
    align-items: flex-start;
    align-self: flex-start;
    flex-direction: row;
    padding-top: 15px;
}

.options {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: flex-start;
}

input[type=checkbox] {
    /* Double-sized Checkboxes */
    -ms-transform: scale(1.5);
    /* IE */
    -moz-transform: scale(1.5);
    /* FF */
    -webkit-transform: scale(1.5);
    /* Safari and Chrome */
    -o-transform: scale(1.5);
    /* Opera */
    transform: scale(1.5);
    padding: 5px;
}

.button-view-all {
    background-color: transparent;
    border: px solid #000;
    /* Add a border to make the button visible */
    color: #000;
    /* Text color */
    padding: 10px 15px;
    /* Adjust padding as needed */
    text-decoration: none;
    /* Remove underline for anchor elements */
    cursor: pointer;
    /* Add cursor pointer for interaction */
    align-self: flex-end;

}

.plant-card {
    display: flex;
    flex-direction: column;
    width: 225px;
    padding: 10px;
    border-radius: 10px;
    background-color: rgb(217, 245, 158);
}

.plant-card-image {
    height: 225px;
    background-image: url('..\\resources\\images\\plant-card.jpg');
}

.plant-name {
    text-align: left;
    font-size: larger;
    font-weight: 600;
    color: #000;
    padding: 2px;
}

.plant-description {
    text-align: left;
    font-size: medium;
    color: #535151;
    padding: 2px;
}

.plant-price {
    text-align: left;
    font-size: medium;
    font-weight: 600;
    color: #000;
    padding: 2px;
}

.column-curiosity {
    display: flex;
    flex-direction: column;
    width: 90%;
    align-self: flex-start;
    align-items: flex-start;
    justify-content: flex-start;
    margin-left: 5%;
    margin-top: 50px;
}

.column-curiosity-inner {
    display: flex;
    flex-direction: column;
    width: 90%;
    align-self: flex-start;
    align-items: flex-start;
    justify-content: flex-start;
    margin-top: 20px;
    margin-bottom: 20px;
}

.curiosity-image {
    height: 400px;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    margin-left: 10px;
}

.curiosity-active {
    display: flex;
    flex-direction: column;
    text-align: left;
    height: 100px;
    width: 100%;
    border-left: solid 2px;
    margin-bottom: 30px;
    padding: 10px;
    opacity: 1;
    transform: scale(1);
    transition: transform 0.5s;
}

.curiosity {
    display: flex;
    flex-direction: column;
    text-align: left;
    height: 100px;
    width: 100%;
    margin-bottom: 30px;
    padding: 10px;
    opacity: 0.4;
    transform: scale(0.6);
    transition: transform 0.5s;
}

.curiosity-title {
    text-align: left;
    font-size: 30px;

}

.product-card {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 350px;
    border-radius: 5px;
    background-color: white;
    margin-block: 10px;
    margin-left: 20px;
    padding: 25px 20px;
    padding-top: 0px;
    position: relative;
    -webkit-box-shadow: 0px -13px 20px -13px rgba(200, 195, 195, 1),
        3px 0px 18px 5px rgba(200, 195, 195, 1),
        0px 25px 20px -25px rgba(200, 195, 195, 1),
        -25px 0px 13px -20px rgba(200, 195, 195, 1);
    box-shadow: 0px -13px 20px -13px rgba(200, 195, 195, 1),
        3px 0px 18px 5px rgba(200, 195, 195, 1),
        0px 25px 20px -25px rgba(200, 195, 195, 1),
        -25px 0px 13px -20px rgba(200, 195, 195, 1);
    height: fit-content;
}

.main {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
}

.main img {
    margin-top: 10px;
    height: 300px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    object-fit: cover;
    /*   float: left; */
}

.main .price {
    width: 70px;
    height: 70px;
    background-color: var(--green);
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    margin-top: -40px;
    margin-left: -10px;
    margin-bottom: 20px;
    z-index: 100;
    position: relative;
}

#main-h2 {
    margin-left: 10px;
    color: var(--dark-text);
    font-size: 1.5rem;
    font-weight: 700;
}

#main-h3 {
    margin-left: 10px;
    font-size: 1.3rem;
    padding: 5px 0px;
    font-weight: 500;
    color: green !important;
    color: var(--green);
    text-align: left;
}

.main-characteristics {
    width: 30%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    margin-left: 60px;
    margin-top: 10px;
}

.main-characteristic {
    display: flex;
    flex-direction: row;
}

.main-characteristic a {
    margin-left: 10px;
    margin-top: 16px;
    font-weight: 600;
}

.main-characteristics img {
    height: 26px;
    background-size: contain;
}

.main-description {
    width: 30%;
}

.main-collpsable {
    width: 80%;
    display: flex;
    margin-top: 50px;
    flex-direction: column;
    justify-content: center;
    margin-left: 40px;
    text-align: left;
}

.seeMoreBtn{
    margin-top: 30px;
}