.App {
  text-align: center;
  overflow-x: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 32px;
  width: 100%;
  position: fixed;
  z-index: 3;
  background-color: #fff;
  box-shadow: 0.3em 0.3em 1em rgba(94, 83, 83, 0.6);
  transition: top 1s ease-out;
  top: 0;
}

.button.is-primary{
  background-color: #a0df3b;
}

.visible {
  /* display: flex; */

  transition: top 0.4s ease-out;
}

.hidden {
  /* display: none; */
  top: -80px;
  transition: top 0.4s ease-out;
}

.header-logo {
  height: 60px;
  width: 100%;
  background-image: url('..\\resources\\images\\logo.jpg');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}



.discover {
  position: relative;
  display: flex;
  z-index: 2;
  background-image: url("..\\resources\\images\\discover.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 500px;
  justify-content: center;
  flex-direction: column;
}

.discover-column {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-left: 70px;
  padding-top: 70px;
  height: fit-content;
  text-align: left;
  font-size: larger;
}

.discover-column h1 {
  display: block;
  font-size: 2em;
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}

.discover-column h5 {
  display: block;
  font-size: 0.83em;
  margin-block-start: 1.67em;
  margin-block-end: 1.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}

@media (max-width: 840px) {
  .discover-column {
    display: flex;
    flex-direction: column;
    padding: 0px;
    margin-bottom: 20px;
  }
}

.discover-row {
  display: flex;
  position: relative;
  flex-direction: row;
  align-self: center;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  gap: 20px;
  width: 90%;
  margin-left: 5%;
  margin-block: 20px;
}

@media (max-width: 840px) {
  .discover-row {
    display: flex;
    flex-direction: column;
    gap: 0px;
    margin: auto;
  }
}

.bg-text {
  background-color: rgb(0, 0, 0);
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/opacity/see-through */
  color: white;
  font-weight: bold;
  border: 2px solid #504c4c;
  position: absolute;
  backdrop-filter: blur(5px);
  z-index: 2;
  width: fit-content;
  margin-left: 10%;
  padding-left: 20px;
  padding-right: 20px;
  text-align: left;
  scale: 1.2;
}

.bg-text h1 {
  display: block;
  font-size: 2em;
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}

.bg-text h5 {
  display: block;
  font-size: 0.83em;
  margin-block-start: 1.67em;
  margin-block-end: 1.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}


@media (max-width: 840px) {
  .bg-text {
    margin-right: 10%;
  }
}

.inner-discover {
