.sidebar {
  position: fixed;
  top: 0;
  height: 100vh;
  width: 80%;
  z-index: 3;
  box-shadow: -10px 0 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: #cdff7c;
  animation-name: sider;
  animation-duration: 500ms;
  transform-origin: top left;
}

.sidebar button {
  border: 1px solid #000;
  /* Add a border to make the button visible */
  /* Text color */

  /* Adjust padding as needed */
  text-decoration: none;
  /* Remove underline for anchor elements */
  display: flex;
  padding: 10px;
  flex-direction: row;
  /* Ensure the button behaves as a block element */
  cursor: pointer;
  align-self: center;
  border-radius: 1em;
  width: fit-content;
  gap: 15px
}


@media (width> 767px) {
  .sidebar {
    display: none
  }
}

@keyframes sider {
  from {
    transform: scaleX(0.0);
    opacity: 0.3;
    border-radius: 0 0% 0% 0;
  }

  to {
    transform: scaleX(1.0);
    opacity: 1.0;
    border-radius: 0;
  }
}

