.still-question {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}

.still-question button {
  background-color: transparent;
  border: px solid #000;
  /* Add a border to make the button visible */
  color: #000;
  /* Text color */
  padding: 10px 15px;
  /* Adjust padding as needed */
  text-decoration: none;
  /* Remove underline for anchor elements */
  cursor: pointer;
  /* Add cursor pointer for interaction */
  margin-block: 10px;
}

@media (max-width: 800px) {
  .still-question button {
    width: 100%;
    left: 0;
  }
}

.still-question div {
  font-size: 18px;
}